@import 'config/variables';

* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  margin: 0;

  &:after {
    content: 'mobile';
    visibility: hidden;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 0px;
    height: 0px;
    z-index: -1;
    pointer-events: none;

    @media (min-width: 768px) {
      content: 'tablet';
    }

    @media (min-width: 1024px) {
      content: 'desktop';
    }
  }
}
